import React, {useEffect, useRef, useState} from "react";
import Lottie from "react-lottie-player";
import animationData from "../lotties/burger-menu.json"
import styled from "styled-components";

const Container = styled.div`
  margin: 0 40px;

  @media (min-width:700px){
    display: none;
  }
`

function MenuButton({onClick, isOpen, staticMenuButton}) {
    const [play, setPlay] = useState(!staticMenuButton);

    useEffect(() => {
        setPlay(!staticMenuButton);
    }, [staticMenuButton])

    function onLoopComplete() {
        setPlay(false);
    }

    return (<Container>
        <Lottie
            key={'MENU_BUTTON'}
            onClick={onClick}
                rendererSettings={{
                    preserveAspectRatio: 'xMidYMid slice'
                }}
                play={play}
                loop={false}
                segments={isOpen ? [0, 50] : [50, 70]}
                onLoopComplete={onLoopComplete}
                animationData={animationData}
                style={{width: 50, height: 50}}
        />
    </Container>)
}

export default MenuButton;
