import React, {useEffect, useRef, useState} from "react";
import Header from "../components/Header";
import BlackBackgroundImgSrc from "../assets/Black-Background.png";
import Placeholder from "../assets/Placeholder.png";
import AppStore from "../assets/Apple.svg";
import GooglePlay from "../assets/GooglePlay.svg"
import MenuView from "./MenuView";
import {useWindowSize} from "../hooks/useWindowSize";
import {
    BackgroundContainer,
    Container,
    ContentContainer, DownloadButtonContainer, FooterContainer,
    JoinContainer, SectionContainer,
    MainContentContainer,
    TitlesContainer, MainContainer, SliderDotContainer
} from "../components/main/styled/containers";
import {H1, H2, JoinNowParagraph} from "../components/main/styled/font";
import {
    Background, ButtonsContainer,
    CreatorFooter,
    DownloadButton,
    FooterSeparator,
    PlaceholderImage,
    SliderDot
} from "../components/main/styled";
import { SLIDES } from "../constants/MainConstants";

function MainPage() {
    const { width } = useWindowSize();

    const [staticMenuButton, setMenuButtonStaticState] = useState(true);

    const [isMobileView, setIsMobileView] = useState(width <= 1100);

    useEffect(() => {
        if (width <= 1100 && !isMobileView) {
            setIsMobileView(true);
        } else if (width > 1100 && isMobileView) {
            setIsMobileView(false);
        }
    }, [width, isMobileView]);

    const [menuOpen, setMenuOpen] = useState(false);

    const [currentSlide, setCurrentSlide] = useState(0);

    function onPushNextSlide() {
        setCurrentSlide(prev => prev + 1 > SLIDES.length - 1 ? 0 : prev + 1);
    }

    function onMenuButtonClick() {
        setMenuOpen(true);
        setMenuButtonStaticState(false);
    }

    function onMenuClose() {
        setMenuOpen(false);
    }

    return (<React.Fragment>
        <Container menuOpen={menuOpen}>
            <Header onMenuButtonClick={onMenuButtonClick} isOpen={menuOpen} staticMenuButton={staticMenuButton} />
            <ContentContainer>
                <MainContentContainer>
                    <BackgroundContainer>
                        <Background src={BlackBackgroundImgSrc}/>
                    </BackgroundContainer>
                    <MainContainer>
                        {!isMobileView && <PlaceholderImage src={Placeholder} />}
                        <SectionContainer>
                            <TitlesContainer>
                                <H1
                                    onAnimationComplete={onPushNextSlide}
                                    key={`H1_${currentSlide}`}
                                    animate={{
                                        scale: [.5, .5, 1, 1, 1],
                                        x: [-50, 0, 0, 0, 50],
                                        opacity: [0, 0, 1, 1, 0],
                                    }}
                                    transition={{
                                        repeat: 0,
                                        duration: 6,
                                        ease: "easeInOut",
                                        times: [0, 0.2, 0.5, 0.8, 1],
                                    }}>{SLIDES[currentSlide].title}</H1>
                                <H2 key={`H2_${currentSlide}`} animate={{
                                    x: [0, 0, 0, 0, -100],
                                    opacity: [0, 0, 1, 1, 0],
                                }}
                                    transition={{
                                        repeat: 0,
                                        duration: 6,
                                        ease: "easeInOut",
                                        times: [0, 0.2, 0.5, 0.8, 1],
                                    }}>{SLIDES[currentSlide].subtitle}</H2>
                                <SliderDotContainer>
                                    {SLIDES.map((_, index) => <SliderDot active={index === currentSlide}/>)}
                                </SliderDotContainer>
                            </TitlesContainer>
                            {isMobileView && <PlaceholderImage src={Placeholder} />}
                            <JoinContainer>
                                <JoinNowParagraph>
                                    Join Snooper Now
                                </JoinNowParagraph>
                                <ButtonsContainer>
                                    <DownloadButtonContainer>
                                        <DownloadButton src={AppStore}/>
                                        App Store
                                    </DownloadButtonContainer>
                                    <DownloadButtonContainer>
                                        <DownloadButton src={GooglePlay}/>
                                        Google Play
                                    </DownloadButtonContainer>
                                </ButtonsContainer>
                            </JoinContainer>
                        </SectionContainer>
                    </MainContainer>
                </MainContentContainer>
                <FooterContainer>
                    <div>
                        © 2022 Snooper
                    </div>
                    <FooterSeparator/>
                    <div>
                        Terms of Service
                    </div>
                    <FooterSeparator/>
                    <div>
                        Privacy Policy
                    </div>
                    <FooterSeparator/>
                    <div>
                        contact@snoopernetwork.com
                    </div>
                </FooterContainer>
                <CreatorFooter>
                    made with ❤️ by the Snooper team
                </CreatorFooter>
            </ContentContainer>
        </Container>
        {menuOpen && <MenuView onMenuClose={onMenuClose}/>}
    </React.Fragment>)
}

export default MainPage;
